@import "global/01-mixins";
@import "fonts";
@import "global/03-vars";
html {
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    line-height: 1;
    font-weight: 400;
    color: #252525;
    position: relative;
    height: 100%;
    background: #fff;
    -webkit-tap-highlight-color: transparent;
}

div,
section,
footer,
.btn {
    box-sizing: border-box;
}

a {
    color: #fff;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus,
input:focus {
    outline: none;
}

p {
    font-weight: normal;
    font-size: 16px;
    line-height: 164%;
    text-align: center;
    color: #000000;
    margin: 0 0 10px 0;
}

input {
    display: block;
    width: 100%;
    font-size: 30px;
    font-weight: 700;
    line-height: 29px;
    padding: 0 8px;
    margin-bottom: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

input:hover,
input:focus {
    outline: none;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

ul li {
    padding: 0 0 0 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

.title {
    color: #000;
    font-size: 42px;
    font-weight: 700;
    line-height: 53px;
    margin-bottom: 20px;
}

.desc {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin-bottom: 15px;
}

img {
    max-width: 100%;
    max-height: 100%;
    display: block;
}

.clearfix:after {
    content: '';
    display: table;
    clear: both;
}

.container {
    max-width: 1562px;
    width: 100%;
    padding: 0 16px;
    margin: 0 auto;
    position: relative;
}

.wrapper {
    position: relative;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.header {
    padding: 0 0 0 0;
}

.header-top {
    padding: 14px 0 14px 0;
    background: #FAFAFA;
    border-bottom: 1px solid #EAEAEA;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.header-top__left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    div,
    a {
        font-weight: normal;
        font-size: 12px;
        line-height: 100%;
        color: #000000;
    }
}

.address {
    background: url(../img/gps.svg) no-repeat left center;
    padding: 0 0 0 18px;
    margin-right: 57px;
}

.worktime {
    background: url(../img/clock.svg) no-repeat left center;
    padding: 0 0 0 20px;
    margin-right: 57px;
}

.mail {
    background: url(../img/mail.svg) no-repeat left center;
    padding: 0 0 0 26px;
    margin-right: 57px;
}

.social {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    a {
        display: block;
        width: 20px;
        height: 20px;
        position: relative;
        margin-right: 41px;
        &:last-child {
            margin-right: 0;
        }
        svg {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            margin: auto;
        }
    }
}

.vk svg {
    width: 15px;
    height: 8px;
}

.inst svg {
    width: 15px;
    height: 14px;
}

.fb svg {
    width: 7px;
    height: 14px;
}

.tube svg {
    width: 16px;
    height: 12px;
}

.vk:hover svg {
    fill: red;
}

.phone {
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: #000000;
    padding: 0 0 0 39px;
    position: relative;
    &::after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: -5px;
        background-color: #EE741D;
        background-image: url(../img/tel-ico.svg);
        background-position: center;
        background-repeat: no-repeat;
        width: 28px;
        height: 28px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    span {
        font-weight: 300;
        font-size: 16px;
        line-height: 100%;
        color: #000000;
    }
}

.header-bottom {
    padding: 25px 0 26px 0;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.header-bottom__left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.menu {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
        position: relative;
        margin-right: 65px;
        &:last-child {
            margin-right: 0;
        }
    }
    a {
        position: relative;
        font-weight: 500;
        font-size: 16px;
        line-height: 100%;
        color: #000000;
    }
}

.sub::after {
    content: '';
    display: block;
    position: absolute;
    top: 9px;
    right: -18px;
    width: 10px;
    height: 7px;
    background: url(../img/sub-arr.svg) no-repeat center;
    @include transition;
}

.sub:hover::after {
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
}

.sub-menu {
    position: absolute;
    z-index: 1;
    overflow: hidden;
    max-height: 0;
    background: #fff;
    padding: 0 20px;
    min-width: 200px;
    transform: translate(-28px, 0px);
    transition: .3s;
}

.menu li.sub:hover ul.sub-menu {
    max-height: 2500px;
}

.logo {
    max-width: 158px;
    margin-right: 51px;
}

.btn-orange {
    display: block;
    max-width: 190px;
    width: 100%;
    padding: 15px 0 16px 0;
    font-weight: bold;
    font-size: 10px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.22em;
    text-transform: uppercase;
    color: #FFFFFF;
    background: #EE741D;
    box-shadow: 0px 8px 26px rgba(255, 153, 58, 0.16);
    border-radius: 23px;
}

.slider-top.slick-dotted {
    margin-bottom: 0;
    .slick-dots {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        bottom: 65px;
        left: 0;
        width: 100%;
        right: 0;
        padding-left: 195px;
        box-sizing: border-box;
    }
    .slick-dots li button:before {
        content: none;
    }
    .slick-dots li {
        width: 12px;
        height: 12px;
        margin: 0 30px 0 0;
        &:last-child {
            margin: 0;
        }
    }
    .slick-dots li button {
        padding: 0;
        opacity: 1;
        width: 12px;
        height: 12px;
        background: rgba(196, 196, 196, 0.25);
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        @include transition;
    }
    .slick-dots .slick-active button {
        background: transparent;
        box-shadow: inset 0 0 0 3px #EE741D;
    }
}

.slider-top__item {
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 640px;
}

.slide-img {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    height: 0;
    padding-bottom: 33.6315%;
    img {
        @include imgProp;
    }
}

.slide-content {
    padding-top: 238px;
    .container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .title {
        font-weight: 500;
        font-size: 41px;
        line-height: 120%;
        width: 50%;
        color: #FFFFFF;
        text-align: left;
        margin-bottom: 28px;
    }
}

.exs-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 83px;
}

.exs-item {
    min-width: 83px;
    max-width: 142px;
}

.exs {
    padding: 88px 0 88px 0;
}

.exs-item-img {
    margin-bottom: 16px;
    img {
        margin: 0 auto;
    }
}

.exs-item-text {
    font-weight: 500;
    font-size: 12px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
}

section {
    .title {
        font-weight: normal;
        font-size: 27px;
        line-height: 120%;
        text-align: center;
        color: #000000;
    }
}

.popular-products {
    padding: 107px 0 115px 0;
    background: #FAFAFA;
    .title {
        text-align: left;
        margin-bottom: 0;
    }
}

.popular-products-top {
    margin-bottom: 27px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
        font-weight: normal;
        font-size: 16px;
        line-height: 100%;
        text-decoration-line: underline;
        color: #000000;
    }
}

.products-items {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}

.products-item {
    max-width: 230px;
    width: 16%;
    margin-right: 30px;
    margin-bottom: 60px;
    &:last-child,
    &:nth-child(6n) {
        margin-right: 0;
    }
}

.products-item-img {
    display: block;
    position: relative;
    height: 230px;
    border: 1px solid #D4D4D4;
    box-sizing: border-box;
    border-radius: 4px;
    margin-bottom: 13px;
    img {
        @include imgProp;
    }
}

.products-item-title {
    font-weight: 500;
    width: 99%;
    font-size: 14px;
    line-height: 150%;
    text-align: left;
    color: #000000;
    margin-bottom: 7px;
}

.products-item-code,
.products-item-series {
    span {
        margin-right: 8px;
    }
    font-weight: 500;
    font-size: 12px;
    line-height: 100%;
    color: #797979;
    margin-bottom: 8px;
}

.products-price {
    font-weight: bold;
    font-size: 14px;
    line-height: 100%;
    color: #EE741D;
}

.catalog {
    padding: 104px 0 121px 0;
    .title {
        margin-bottom: 29px;
    }
}

.about {
    padding: 120px 0 110px 0;
    margin-bottom: 116px;
    background: #FAFAFA;
    .title {
        margin-bottom: 19px;
    }
    p {
        max-width: 997px;
        width: 100%;
        margin: 0 auto;
        margin-bottom: 10px;
        font-weight: normal;
        font-size: 16px;
        text-align: center;
        color: #000000;
    }
}

.catalog-items {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}

.catalog-item-img {
    margin-bottom: 30px;
    img {
        margin: 0 auto;
    }
}

.catalog-item {
    display: flex;
    margin-right: 30px;
    margin-bottom: 25px;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: 230px;
    padding: 50px 30px 23px 30px;
    max-width: 230px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 8px 14px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
    box-sizing: border-box;
    @include transition;
    &:last-child,
    &:nth-child(6n) {
        margin-right: 0;
    }
    &:hover {
        box-shadow: 0px 10px 26px rgba(0, 0, 0, 0.04);
    }
}

.catalog-item-name {
    font-weight: 500;
    font-size: 12px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
}


/* I E   S T Y L E S */

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    .block {
        width: 100%;
    }
}

#hamburger-icon {
    position: relative;
    display: none;
    width: 18px;
    height: 14px;
}

#hamburger-icon .line {
    position: absolute;
    left: 0;
    display: block;
    background: #ff6543;
    width: 18px;
    height: 2px;
    transition: .4s;
    -webkit-transition: .4s;
    -moz-transition: .4s;
}

.mobile-menu {
    display: none;
}

.line.line-1 {
    top: 0;
}

.line.line-3 {
    top: 100%;
}

.line.line-2 {
    top: 50%;
}

#hamburger-icon.active .line-1 {
    transform: translateY(7px) translateX(0) rotate(45deg);
    -webkit-transform: translateY(7px) translateX(0) rotate(45deg);
    -moz-transform: translateY(7px) translateX(0) rotate(45deg);
    -ms-transform: translateY(7px) translateX(0) rotate(45deg);
    -o-transform: translateY(7px) translateX(0) rotate(45deg);
}

#hamburger-icon.active .line-2 {
    opacity: 0;
}

#hamburger-icon.active .line-3 {
    transform: translateY(-7px) translateX(0) rotate(-45deg);
    -webkit-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -moz-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -ms-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -o-transform: translateY(-7px) translateX(0) rotate(-45deg);
}

.ov-hidden {
    overflow: hidden;
}

.fade {
    -webkit-transition: opacity .15s linear;
    transition: opacity .15s linear
}

.fade:not(.show) {
    opacity: 0
}

@media (min-width:576px) {
    .modal-dialog {
        margin: 0 auto;
        max-width: 655px
    }
}

.modal-dialog {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    max-width: 655px;
    position: relative;
    width: auto;
    height: 100vh;
    pointer-events: none
}

.modal-content {
    background: #fff;
    position: relative;
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    max-width: 655px;
    width: 100%;
    pointer-events: auto;
    border: none;
    outline: 0
}

.close:not(:disabled):not(.disabled) {
    cursor: pointer
}

.modal-open .modal {
    overflow-x: hidden;
    overflow-y: auto
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    display: none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;
    background: rgba(10, 22, 30, 0.9);
}

.modal.fade .modal-dialog {
    -webkit-transition: -webkit-transform .3s ease-out;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
    -webkit-transform: translate(0, -50px);
    transform: translate(0, -50px);
    -webkit-transition: transform .3s ease-out;
    -moz-transition: transform .3s ease-out;
    -ms-transition: transform .3s ease-out;
    -o-transition: transform .3s ease-out
}

.modal.fade .modal-dialog {
    -webkit-transition: -webkit-transform .3s ease-out;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
    -webkit-transform: translate(0, -50px);
    transform: translate(0, -50px);
    -webkit-transition: transform .3s ease-out;
    -moz-transition: transform .3s ease-out;
    -ms-transition: transform .3s ease-out;
    -o-transition: transform .3s ease-out
}

.modal.show .modal-dialog {
    -webkit-transform: none;
    transform: none
}

.modal-close {
    position: absolute;
    width: 21px;
    height: 21px;
    background: url(../img/close.svg) no-repeat center;
    right: 31px;
    top: 31px;
    font-size: 25px;
    font-weight: 400;
    text-shadow: none;
    cursor: pointer;
    -webkit-transition: all .3s;
    transition: all .3s;
    opacity: 1
}

.form-popup {
    padding: 45px 45px 60px 45px;
}

.modal-open {
    overflow: hidden;
    padding-right: 17px!important;
}

@media screen and (min-width:992px) {
    .btn-orange:hover {
        background: #ec4824;
    }
    .modal-close:hover {
        opacity: 0.7;
    }
}


/* 
    M O B I L E
*/

@media screen and (max-width: 1200px) {
    html,
    body {
        min-width: 320px;
        width: 100%;
        overflow-x: hidden;
    }
    a:focus {
        outline: none;
    }
}

@media screen and (max-width: 1199px) {}

@media screen and (max-width: 991px) {
    body {
        padding-top: 68px;
    }
    .header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background: #152027;
        z-index: 99;
        width: 100%;
        box-shadow: 0 8px 20px rgba(200, 200, 200, .1);
    }
    #hamburger-icon {
        display: block;
    }
    .mobile-menu {
        position: fixed;
        top: 68px;
        right: 0;
        width: 100vw;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        height: calc(100vh - 68px);
        z-index: 30;
        overflow: auto;
        background: rgba(21, 32, 39, 0.95);
        padding: 35px 15px 25px;
        box-sizing: border-box;
        // box-shadow: inset 0 29px 33px rgba(0, 86, 189, .1);
        transform: translate(100vw, 0);
        -webkit-transform: translate(100vw, 0);
        -moz-transform: translate(100vw, 0);
        -ms-transform: translate(100vw, 0);
        -o-transform: translate(100vw, 0);
        transition: .3s ease-in-out;
        -webkit-transition: .3s ease-in-out;
        -moz-transition: .3s ease-in-out;
        -ms-transition: all .3s ease-in-out;
        -o-transition: .3s ease-in-out;
    }
    .mobile-menu.active {
        transform: translate(0, 0);
        -webkit-transform: translate(0, 0);
        -moz-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
        -o-transform: translate(0, 0);
    }
    .mobile-menu .menu {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
    }
    .mobile-menu .menu li {
        display: block;
        margin: 0 0 20px 0;
    }
    .mobile-menu .menu a {
        font-size: 24px;
    }
}

@media screen and (max-width: 767px) {
    .container {
        width: 320px;
    }
    .modal-content {
        margin: 0 15px;
    }
}

